"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";

export default function WalletPage() {
  const router = useRouter();
  const [user, setUser] = useState<any>(null);
  const [walletAddress, setWalletAddress] = useState<string | null>(null);
  const [userBalance, setUserBalance] = useState<string>("0.00");
  const [transactions, setTransactions] = useState<any[]>([]);

  useEffect(() => {
    // Check auth
    const token = localStorage.getItem("accessToken");
    if (!token) {
      router.push("/login");
      return;
    }

    // Fetch user data
    fetchUserData();
  }, []);

  const fetchUserData = async () => {
    try {
      const token = localStorage.getItem("accessToken");
      const res = await fetch("/api/auth/me", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (!res.ok) {
        router.push("/login");
        return;
      }

      const data = await res.json();
      setUser(data);
      generateUserWallet(data.id);
    } catch (error) {
      console.error("Error fetching user:", error);
      router.push("/login");
    }
  };

  const generateUserWallet = (userId?: string) => {
    // Generate a deterministic wallet address based on user ID
    const id = userId || user?.id || "default";
    const hash = btoa(id + Date.now()).substring(0, 40);
    const address = "0x" + hash.padEnd(40, "0").substring(0, 40);
    setWalletAddress(address);
  };

  const handleLogout = () => {
    localStorage.removeItem("accessToken");
    localStorage.removeItem("refreshToken");
    router.push("/login");
  };

  const copyAddress = () => {
    if (walletAddress) {
      navigator.clipboard.writeText(walletAddress);
      alert("Address copied to clipboard!");
    }
  };

  if (!user) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Loading...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Sidebar */}
      <aside className="fixed inset-y-0 left-0 w-64 bg-white border-r border-gray-200">
        <div className="p-6">
          <h1 className="text-2xl font-bold text-blue-600">Demo Pay</h1>
        </div>
        <nav className="px-4 space-y-1">
          <NavItem href="/dashboard" icon="ðŸ“Š" label="Dashboard" />
          <NavItem href="/wallet" icon="ðŸ‘›" label="Wallet" active />
          <NavItem href="/payments" icon="ðŸ’³" label="Payments" />
          <NavItem href="/bookings" icon="ðŸ“…" label="Bookings" />
          <NavItem href="/reports" icon="ðŸ“ˆ" label="Reports" />
          <NavItem href="/settings" icon="âš™ï¸" label="Settings" />
        </nav>
        <div className="absolute bottom-0 left-0 right-0 p-4 border-t border-gray-200">
          <div className="flex items-center space-x-3 mb-3">
            <div className="w-10 h-10 bg-blue-100 rounded-full flex items-center justify-center">
              <span className="text-blue-600 font-semibold">
                {user.firstName?.[0]}
                {user.lastName?.[0]}
              </span>
            </div>
            <div className="flex-1 min-w-0">
              <p className="text-sm font-medium text-gray-900 truncate">
                {user.firstName} {user.lastName}
              </p>
              <p className="text-xs text-gray-500 truncate">
                {user.facility?.name}
              </p>
            </div>
          </div>
          <button
            onClick={handleLogout}
            className="w-full text-sm text-red-600 hover:text-red-700 font-medium"
          >
            Sign Out
          </button>
        </div>
      </aside>

      {/* Main Content */}
      <main className="ml-64 p-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900">Your Wallet</h1>
          <p className="text-gray-600 mt-1">
            Manage your digital wallet and payment methods
          </p>
        </div>

        {/* Wallet Card */}
        <div className="bg-gradient-to-r from-blue-600 to-cyan-500 rounded-2xl shadow-lg p-8 mb-8 text-white">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-xl font-semibold">Digital Wallet</h2>
            <div className="bg-white/20 px-3 py-1 rounded-full text-sm">
              Active
            </div>
          </div>

          <div className="mb-8">
            <p className="text-sm text-blue-100 mb-2">Wallet Address</p>
            <div className="bg-white/10 p-4 rounded-lg border border-white/20">
              <div className="font-mono text-lg break-all">{walletAddress}</div>
            </div>
          </div>

          <div className="grid grid-cols-2 gap-6">
            <div>
              <p className="text-sm text-blue-100 mb-1">Total Balance</p>
              <p className="text-3xl font-bold">${userBalance}</p>
            </div>
            <div>
              <p className="text-sm text-blue-100 mb-1">Network</p>
              <p className="text-lg font-semibold">Ethereum Mainnet</p>
            </div>
          </div>

          <div className="flex gap-4 mt-8">
            <button
              onClick={copyAddress}
              className="px-6 py-3 bg-white text-blue-600 rounded-lg font-semibold hover:bg-blue-50 transition"
            >
              Copy Address
            </button>
            <button className="px-6 py-3 bg-white/20 text-white rounded-lg font-semibold hover:bg-white/30 transition border border-white/30">
              Connect MetaMask
            </button>
          </div>
        </div>

        {/* Quick Actions */}
        <div className="bg-white rounded-lg shadow-sm p-6 mb-8">
          <h2 className="text-lg font-semibold mb-4">Quick Actions</h2>
          <div className="grid grid-cols-3 gap-4">
            <Link
              href="/payments/new"
              className="flex flex-col items-center justify-center p-4 border-2 border-gray-200 rounded-lg hover:border-blue-500 hover:bg-blue-50 transition"
            >
              <span className="text-3xl mb-2">ðŸ’³</span>
              <span className="text-sm font-medium text-gray-700">
                Send Payment
              </span>
            </Link>
            <Link
              href="/payments"
              className="flex flex-col items-center justify-center p-4 border-2 border-gray-200 rounded-lg hover:border-blue-500 hover:bg-blue-50 transition"
            >
              <span className="text-3xl mb-2">ðŸ“¥</span>
              <span className="text-sm font-medium text-gray-700">
                Receive Payment
              </span>
            </Link>
            <Link
              href="/bookings"
              className="flex flex-col items-center justify-center p-4 border-2 border-gray-200 rounded-lg hover:border-blue-500 hover:bg-blue-50 transition"
            >
              <span className="text-3xl mb-2">ðŸ“…</span>
              <span className="text-sm font-medium text-gray-700">
                Book Appointment
              </span>
            </Link>
          </div>
        </div>

        {/* Transaction History */}
        <div className="bg-white rounded-lg shadow-sm p-6">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-lg font-semibold">Transaction History</h2>
            <Link
              href="/ledger"
              className="text-sm text-blue-600 hover:text-blue-700"
            >
              View all â†’
            </Link>
          </div>

          {transactions.length === 0 ? (
            <div className="text-center py-8 text-gray-500">
              <div className="text-4xl mb-4">ðŸ“„</div>
              <p className="font-medium">No transactions yet</p>
              <p className="text-sm mt-2">
                Your transaction history will appear here
              </p>
              <Link
                href="/payments/new"
                className="inline-block mt-4 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition"
              >
                Make First Payment
              </Link>
            </div>
          ) : (
            <div className="space-y-3">
              {/* Transaction items would go here */}
            </div>
          )}
        </div>
      </main>
    </div>
  );
}

function NavItem({
  href,
  icon,
  label,
  active = false,
}: {
  href: string;
  icon: string;
  label: string;
  active?: boolean;
}) {
  return (
    <Link
      href={href}
      className={`flex items-center space-x-3 px-4 py-3 rounded-lg transition ${
        active
          ? "bg-blue-50 text-blue-600 font-medium"
          : "text-gray-700 hover:bg-gray-50"
      }`}
    >
      <span className="text-xl">{icon}</span>
      <span>{label}</span>
    </Link>
  );
}
